.login {
    margin: 70px auto 0;
    max-width: 350px;
}
.login__header {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-content: center;
}
.header__title {
    font-size: 18px;
    font-weight: normal;
    margin-bottom: 10px;
}
.header__create {
    font-size: 13px;
    margin-top: 5px;
    line-height: normal;
}
.form {
    display: block;
}

.form__field {
    padding: 10px;
    position: relative;
}

.field__input {
    margin: 2px 0 5px;
    min-height: 34px;
    line-height: normal;
    box-sizing: border-box;
    border-radius: 3px;
    -webkit-appearance: none;
    font-size: 14px;
    border: 1px solid rgba(0,0,0,0.15);
    padding: 5px 6px;
    width: 100%;
}

.form__actions {
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.actions__forgot-password {
    font-size: 13px;
}
