.forgot-password {
  margin: 70px auto 0;
  max-width: 350px;
}
.forgot-password__header {
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.forgot-password__para {
  padding: 0 10px;
  font-size: 16px;
  text-align: left;
}
.header__title {
  font-size: 18px;
  font-weight: normal;
  margin-bottom: 10px;
}

.header__link {
    font-size: 13px;
    margin-top: 5px;
    line-height: normal;
}

.form {
  display: block;
}

.form__field {
  padding: 10px;
  position: relative;
}

.field__input {
  margin: 2px 0 5px;
  min-height: 34px;
  line-height: normal;
  box-sizing: border-box;
  border-radius: 3px;
  -webkit-appearance: none;
  font-size: 14px;
  border: 1px solid rgba(0, 0, 0, 0.15);
  padding: 5px 6px;
  width: 100%;
}

.form__actions {
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.field__error {
    color: red;
    font-size: 13px;
    text-align: left;
    padding: 0;
    margin: 0;
    position: absolute;
}
